
/*
@author : Deloitte
Service class to perform Sill related operations.
*/
import { Injectable } from '@angular/core';
import { Observable, of } from "rxjs";
import { CodeNumValue } from '../../tdl/models/codenumvalue.model';
import { tap, catchError } from 'rxjs/operators';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
}

/**
 *
 *
 * @export
 * @class SkillService
 */
@Injectable()
export class SkillService {

    private getskillsurl = environment.apiurl + '/api/v1/skill';

    constructor(
        private http: HttpClient
    ) { }

    public saveSkills(resourceId: number, skills: CodeNumValue[]): Observable<CodeNumValue[]> {
        return this.http.post(this.getskillsurl + '/' + resourceId, skills, httpOptions).pipe(
			tap(_ => this.log("saveSkills")),
			catchError(this.handleError<any>("saveSkills"))
		);
    }

    public saveSkillResultsJSON(resourceId: number, skillJSON: any): Observable<CodeNumValue[]> {
        return this.http.post(this.getskillsurl + '/json/' + resourceId, skillJSON, httpOptions).pipe(
			tap(_ => this.log("saveSkillResultsJSON")),
			catchError(this.handleError<any>("saveSkillResultsJSON"))
		);
    }

    public getskillResultsJSON(resourceId: number) {
        return this.http.get(this.getskillsurl + '/json/' + resourceId, httpOptions).pipe(
			tap(_ => this.log("getskillResultsJSON")),
			catchError(this.handleError<any>("getskillResultsJSON"))
		);
    }

    public getAllSkills(){
        return this.http.get(this.getskillsurl, httpOptions).pipe(
			tap(_ => this.log("getAllSkills")),
			catchError(this.handleError<any>("getAllSkills"))
		);
    }

    public getSkillsByResourceId(resourceId:number){
        return this.http.get(this.getskillsurl + '/skillbyres/' + resourceId, httpOptions).pipe(
			tap(_ => this.log("getSkillsByResourceId")),
			catchError(this.handleError<any>("getSkillsByResourceId"))
		);
    }

    public getResourcesBySkillCode(skillCode:string){
        return this.http.get(this.getskillsurl + '/resbyskill/' + skillCode, httpOptions).pipe(
			tap(_ => this.log("getResourcesBySkillCode")),
			catchError(this.handleError<any>("getResourcesBySkillCode"))
		);
    }
  	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		//this.messageService.add("Profile Service: ${message}");
	}
}
